/* //header  */
.header_detail-row{
    background-position: center center;
    padding: 30px 40px;
    border-radius: 3px;
    animation: showup linear 1s;
    min-height: 200px;
    background-size: cover;
    background-repeat: no-repeat;
    transition: all 1s;
    width: 100%;
    height: 480px;
    position: relative;
    margin-top: -88px;
}

.ant-image{
    border-radius: 8px;
    overflow: hidden;
}
.header_detail-col-1{
    top: 50% !important;
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
}   
.header_detail--trailer{
    width: 102px;
    height: 45px;
    background: rgba(3,37,65, 1);
    margin: 8px;
    font-size: 20px;
    border: none;
    font-weight: 600;
    border-radius: 4px;
    cursor: pointer;
    overflow: hidden;
}

.header_detail--trailer:hover{
    background: rgba(3,37,65, 1)!important;
    opacity: 0.9!important;
    border: none!important;
}

.header_detail--trailer > span{
    background: linear-gradient(to right, #c0fecf 0%, #1ed5a9 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    cursor: pointer;
}

.header_detail-col-2{
    width: auto;
    height: 225px;
    top: 50%;
}

.header_detail-col-2--title{
    color: rgba(255,255,255,0.9);
    font-size: 2.2rem;
    font-weight: 700;
}

.div-custom,
h2{
    margin: 0px !important;
}

.header_detail-col-2--genres--title{
    border: 1px solid rgba(255,255,255,0.6);
    color: rgba(255,255,255,0.6);
    padding: 4px 6px;
    display: inline-flex;
    white-space: nowrap;
    align-items: center;
    align-content: center;
    font-size: 15px;
    margin-right: 5px;
}

.header_detail-col-2--genres > a {
    color: #fff;
    text-decoration: none;
    font-weight: 500;
    background: transparent;
    font-size: 15px;
    margin: 0 3px;
}

.header_detail-col-2--genres > a:hover{
    opacity: 0.8;
    color: #fff!important;
}

.header_detail-col-2--genres--runtime{
    color: #fff;
    text-decoration: none;
    font-weight: 500;
    background: transparent;
    font-size: 15px;
    margin: 0 3px;
}

.circular_detail{
    width: 66px;
    height: 66px;
    margin: 12px 0 0 0!important;
    display: inline-block;
}

.header_detail__vote{
    display: flex;
    align-items: center;
    justify-items: center;
}

.user-score{
    color: #fff;
    margin:0 0 0 10px;
    font-size: 20px;
    width: 70px;
    text-align: center;
}

.header_detail__rate{
    margin-top: 8px !important;
}

/* container */

.container_row{
    min-height: 250px;
}